<template>
  <div class="stereograph shadow-box">
    <router-link to="/" class="arrowLeft">
      &lt; 
    </router-link>
    <div id="viewer"></div>
  </div>
</template>

<script>
import { ref, onMounted, onUnmounted } from "vue";
import { Viewer } from "photo-sphere-viewer";
import "photo-sphere-viewer/dist/photo-sphere-viewer.css";
// 
import { MarkersPlugin } from "photo-sphere-viewer/dist/plugins/markers";
import "photo-sphere-viewer/dist/plugins/markers.css";

// 
import region from "@/assets/region.jpg";
import indoor1 from "@/assets/indoor1.jpg";
import indoor2 from "@/assets/indoor2.jpg";
import indoor3 from "@/assets/indoor3.jpg";

export default {
  name: "Stereograph",
  setup() {
    let viewer = ref(null);

    onMounted(() => {
      viewer.value = new Viewer({
        container: document.querySelector("#viewer"),
        panorama: region,
        navbar: ["autorotate", "zoom", "fullscreen"], // 
        lang: {
          // 
          autorotate: "", // 
          zoom: "", // 
          zoomOut: "", // 
          zoomIn: "", // 
          move: "", // 
          download: "", // 
          fullscreen: "", // 
          menu: "", // 
          twoFingers: "", // 
          ctrlZoom: "ctrl + ", // ctrl + 
          loadError: "", // 
        },
        defaultZoomLvl: 1, // 
        autorotateDelay: 1500, // 
        autorotateIdle: true, // 
        autorotateSpeed: 0.1, // 
        mousewheelCtrlKey: false, // 

        // 
        plugins: [
          [
            MarkersPlugin,
            {
              markers: [
                {
                  id: "goIndoor1",
                  tooltip: "1",
                  circle: 8,
                  style: {
                    fill: "rgba(255,255,255,0.8)",
                  },
                  longitude: 3.09,
                  latitude: -0.25,
                },
              ],
            },
          ],
        ],
      });

      let markersPlugin =
        viewer.value.getPlugin(MarkersPlugin);

      markersPlugin.on(
        "select-marker",
        (e, marker, data) => {
          switch (marker.id) {
            case "goIndoor1":
              viewer.value.setPanorama(indoor1);
              markersPlugin.removeMarker(marker);
              markersPlugin.addMarker({
                id: "goIndoor2",
                tooltip: "",
                circle: 8,
                style: {
                  fill: "rgba(255,255,255,0.8)",
                },
                longitude: 4.6,
                latitude: 0.1,
              });
              break;
            case "goIndoor2":
              viewer.value.setPanorama(indoor2);
              markersPlugin.removeMarker(marker);
              markersPlugin.addMarker({
                id: "goIndoor3",
                tooltip: "",
                circle: 8,
                style: {
                  fill: "rgba(255,255,255,0.8)",
                },
                longitude: 0.32,
                latitude: -0.0,
              });
              break;
            case "goIndoor3":
              viewer.value.setPanorama(indoor3);
              markersPlugin.removeMarker(marker);
              markersPlugin.addMarker({
                id: "goRegion",
                tooltip: "",
                circle: 8,
                style: {
                  fill: "rgba(255,255,255,0.8)",
                },
                longitude: -0.005,
                latitude: 0.1,
              });
              break;
            case "goRegion":
              viewer.value.setPanorama(region);
              markersPlugin.removeMarker(marker);
              markersPlugin.addMarker({
                id: "goIndoor1",
                tooltip: "",
                circle: 8,
                style: {
                  fill: "rgba(255,255,255,0.8)",
                },
                longitude: 3.09,
                latitude: -0.25,
              });
            default:
              break;
          }
        },
      );
    });

    onUnmounted(() => {
      viewer.value = null;
    });

    return { viewer };
  },
};
</script>

<style lang="less" scoped>
.arrowLeft {
  position: absolute;
  top: 20px;
  left: 25px;
  text-decoration: none;
  color: #000;
  z-index: 100;
  cursor: pointer;
}

#viewer {
  width: 100%;
  height: 100%;
}
</style>
